export default {
	data() {
		return {
			title: 'Hello'
		};
	},
	onLoad() {},
	methods: {
		gotpLogin() {
			// 通过此方式改变全局值
			//this.$u.vuex('vuex_version', '诗圣');

			this.$u.route({
				url: 'pages/login/login',
				animationDuration: 200
			});
		},
		gotpMyPage() {
			this.$u.route({
				url: 'pages/mypage/mypage',
				animationDuration: 200
			});
		}
	}
};
